import React, { Component } from 'react';

class Descricao extends Component {

    state = { selected: 'descricao' }

    renderCaracteristicas(){
        return(
            <div className="caracteristicas container-small">
                <table>
                    <tbody>
                        <tr>
                            <td>Linha</td>
                            <td>Converse</td>
                        </tr>
                        <tr>
                            <td>Cor</td>
                            <td>Preto/Black</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }

    renderDescricao(){
        return (
            <div className="descricao container-small">
                <p>
                    Um dos modelos de tênis mais vendidos em todo o mundo, de uma marca que está no mercado a mais de 100 anos, o CT All Star Core Ox, da linha Chuck Taylor, é um clássico que nunca sai de moda. Cabedal todo feito em tecido lona 100% algodão, furações na lateral para melhor circulação de ar, solado de borracha vulcanizada com logotipo na traseira, a clássica biqueira também de borracha e palmilha em espuma de EVA com camada reforçada em poliuretano na região calcanhar.
                </p>
                <p>
                    Tênis Converse CT All Star Core Ox Preto Listra Vermelha, feito para todos os estilos e gostos!
                </p>
                <br />
                <div className="flex flex-center">
                    <img src={`${process.env.PUBLIC_URL}/media/tamanhos.png`} alt="sizes" width="400px" />
                </div>
                <br />
                <p>
                    A CONVERSE Inc. é sediada em Massachusetts. Fundada em 1908, a empresa construiu uma forte reputação com produtos como Chuck Taylor All Star, entre outros. Hoje, oferece um diverso portfólio, comercializado em mais de 160 países.
                </p>
                <p>
                    Desde a época do Chuck Taylor All Star, velho conhecido dos skatistas, a Converse vinha trabalhando de forma rápida com o skate. Porém, a partir de 2008, ano de seu centenário, a marca resolveu tratar o skate com mais cuidado e atenção, com uma divisão nos EUA, no Brasil e no mundo.
                </p>
            </div>
        )
    }

    changeSelect = (selected) => this.setState({ selected });

    render(){
        const {selected} = this.state
        return (
            <div className="Descricao">
                <div className="descricao-header flex horizontal-only">
                    <div className={"flex-1 flex flex-center"+( selected === 'descricao' ? ' active': '' )} onClick={()=>this.changeSelect('descricao')}>
                        <h3>Descrição</h3>
                    </div>
                    <div className={"flex-1 flex flex-center"+( selected === 'caracteristicas' ? ' active': '' )} onClick={()=>this.changeSelect('caracteristicas')}>
                        <h3>Características</h3>
                    </div>
                </div>
                <div className="descricao-body flex">
                    <br /><br />
                    { selected === 'descricao' && this.renderDescricao() }
                    { selected === 'caracteristicas' && this.renderCaracteristicas() }
                </div>
            </div>
        );
    }
}

export default Descricao;